@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body {
  overflow: auto;
}

.App {
  justify-content: center;
  padding-bottom: 3rem;
}

.App-link {
  color: #61dafb;
}

.App-header {
  background-color: #282c34;
  height: 60px;
  display: flex;
  align-items: center;
  color: white;
  position: fixed;
  width: 100%;
}

.App-logo {
  height: 40px;
  pointer-events: none;
}

#brand a {
  display: flex;
  align-items: center;
  margin-left: 3rem;
  text-decoration: none;
}

#brand a h1 {
  display: inline;
  font-size: 2rem;
  color: #54b4cf;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#sections {
  margin-left: 5rem;
  display: block;
  height: 100%;
}

#sections a {
  color: white;
  /* background-color: black; */
  text-decoration: none;
  padding: 1rem 1.5rem;
  margin-right: 0.5rem;
  /* height: 100%; */
  display: inline-block;
}

#sections a:hover {
  color: #54b4cf;
}

#sections a:focus {
  border-bottom: 3px solid #54b4cf;
}

#sections ul {
  list-style: none;
  margin: 0;
  height: 100%;
}

#sections ul li {
  display: inline-block;
  margin-right: 0.4rem;
  /* height: max-content; */
  padding: 1rem 1rem;
  /* background-color: red; */
}

#sections ul li a {
  color: white;
  display: block;
  height: 100%;
  /* background-color: black; */
  text-decoration: none;
}

#sections ul li:hover > a {
  color: #54b4cf;
}

#navigation {
  display: none;
}

#mobile-navButton {
  display: none;
}

/* > 965px corta los links  */
@media (max-width: 965px) {
  #brand a {
    margin-left: 20%;
  }
  #mobile-navButton {
    display: block;
  }
  #sections {
    display: none;
  }
}

#navigation {
  position: fixed;
  top: 0;
  right: 0;
  right: -200px;
  width: 60vw;
  height: 100vh;
  background-color: #282C34;
  z-index: 10;
  display: grid;
  place-items: center;
  transition: 0.5s;
}

#navigation a {
  display: block;
  text-align: center;
  text-decoration: none;
  color: black;
  font-size: 1.2rem;
  font-weight: 500;
  background-color: rgb(175, 175, 175);
  /* padding: 10px; */
  width: -webkit-min-content;
  width: -moz-min-content;
  width: min-content;
  padding: 4px 8px;
  border: 1px solid black;
  border-radius: 10px;
  z-index: 100;
}

#navigation.show {
  right: 0;
}
#navigation.notShow {
  right: -100%;
}

#mobile-navButton {
  margin-left: auto;
  margin-right: 20px;
}

#mobile-navButton button {
  position: relative;
  z-index: 100;
  padding: 10px;
}

#filtrador {
  /* background-color: red; */
  display: flex;
  justify-content: center;
  margin-bottom: 0.5rem;
}

#filtrador form {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

#filtrador input[type="text"] {
  flex-grow: 3;
  display: inline-block;
  width: 70%;
  margin: 0 80px;
  margin-bottom: 0.5rem;
  padding: 10px;
  outline: none;
  border-radius: 1rem;
}

@media (max-width: 781px) {
  #filtrador input[type="text"] {
    width: auto;
  }
}

.message {
  margin-left: 1rem;
  text-align: center;
}

.switch {
  flex-shrink: 1;
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: 0.4s;
  z-index: -1;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: #2196f3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

