.no-stock {
  color: red;
}

#productos {
  border-collapse: collapse;
  width: 100%;
}

#productos td,
#productos th {
  border: 1px solid #ddd;
  padding: 8px;
}

#productos tr:nth-child(even) {
  background-color: #f2f2f2;
}

#productos tr:hover {
  background-color: #ddd;
}

#productos th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: #4caf50;
  color: white;
}

.category {
  background-color: rgb(48, 39, 39);
  color: white;
}