#filtrador {
  /* background-color: red; */
  display: flex;
  justify-content: center;
  margin-bottom: 0.5rem;
}

#filtrador form {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

#filtrador input[type="text"] {
  flex-grow: 3;
  display: inline-block;
  width: 70%;
  margin: 0 80px;
  margin-bottom: 0.5rem;
  padding: 10px;
  outline: none;
  border-radius: 1rem;
}

@media (max-width: 781px) {
  #filtrador input[type="text"] {
    width: auto;
  }
}

.message {
  margin-left: 1rem;
  text-align: center;
}

.switch {
  flex-shrink: 1;
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  z-index: -1;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: #2196f3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}
